body {
  padding: 0;
  margin: 0;
  background-image: url("background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
h1, h2, p, ul, li {
  font-family:sans-serif;
}
ul.header li {
  display: inline-block;
  list-style-type: none;
  margin: 0;
}
ul.header {
  display: -ms-flexbox;
  display: flex;
  margin: 0; 
  padding: 0;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

ul.header li a {
  color: #FFF;
  font-family: DejaVu Sans Mono, monospace;
  text-decoration: none;
  font-size: 100%;
  padding: 15px;
  display: -ms-flexbox;
  display: flex;
}


.content {
  background-color: #FFF;
  padding: 40px;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}



